.collection-container {
  height: 50vmin;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.App-logo {
  height: 50vmin;
  pointer-events: none;
  position: fixed;
  margin:auto;
  left:0;
  right:0;
}